.MarkerStyle{
    border: 1px solid white;
    border-radius: 50%;

    height: 10px;
    width: 10px;

    transform: translate(-50%, -50%);

    background-color: blue;
    cursor: pointer;
    z-index: 10;
}

.MarkerStyle.active{
    border: 1px solid white;
    border-radius: 50%;

    height: 10px;
    width: 10px;

    transform: translate(-50%, -50%);

    background-color: red;
    cursor: pointer;
    z-index: 10;
}